@keyframes roll-in-left {
  0% {
    transform: translateX(-50px) rotate(-540deg);
    opacity: 0;
  }
  to {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}
@keyframes jello-horizontal {
  0%,
  to {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
}
.locouiLogoIcon {
  position: relative;
  border-radius: 8.03px;
  width: 2.19rem;
  height: 2.19rem;
  overflow: hidden;
  flex-shrink: 0;
}
.locouiLogoIcon:hover {
  animation: 3s ease-in-out 2s 1 alternate none roll-in-left;
  opacity: 1;
}
.locouiIcon {
  position: relative;
  width: 3.59rem;
  height: 0.85rem;
  display: none;
}
.byTeamLocofy {
  position: relative;
  letter-spacing: -0.04em;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.locouiParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 0.25rem;
}
.locouiLogo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-11xs);
}
.learnMore,
.resources {
  position: relative;
  display: inline-block;
  width: 10.77rem;
  flex-shrink: 0;
}
.learnMore:hover {
  color: var(--color-orange);
  animation: 2.5s ease-in-out 0s 1 normal both jello-horizontal;
  opacity: 1;
  font-weight: 700;
  text-decoration: underline;
}
.affiliateProgramsContainer:hover,
.ourFuture:hover,
.resources:hover {
  color: var(--color-orange);
  animation: 2.5s ease-in-out 0s 1 normal none jello-horizontal;
  opacity: 1;
  font-weight: bolder;
  text-decoration: underline;
}
.affiliatePrograms {
  margin: 0;
}
.affiliateProgramsContainer {
  text-decoration: none;
  color: inherit;
}
.affiliateProgramsContainer:hover,
.ourFuture:hover {
  font-weight: 700;
}
.affiliateProgramsContainer,
.ourFuture {
  position: relative;
  display: inline-block;
  width: 10.77rem;
  flex-shrink: 0;
}
.learnMoreParent {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xl);
}
.frameWrapper {
  position: relative;
  width: 47.56rem;
  height: 2.38rem;
}
.frameChild {
  position: absolute;
  height: 72%;
  width: 70.27%;
  top: 4%;
  right: 14.87%;
  bottom: 24%;
  left: 14.86%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.frame {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  width: 2.19rem;
  height: 1.56rem;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.frameParent {
  width: 47.56rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--text-large-size);
  color: var(--dominant);
}
.blankPayolaPage,
.navBar {
  display: flex;
  box-sizing: border-box;
  align-items: center;
}
.navBar {
  background-color: var(--color-gray-900);
  width: 100vw;
  height: 4.81rem;
  flex-direction: row;
  padding: var(--padding-3xl) var(--padding-69xl);
  justify-content: space-between;
  position: sticky;
  top: 0;
  text-align: left;
  font-size: var(--text-medium-size);
  color: var(--light-background);
  font-family: var(--text-small);
}
.blankPayolaPage {
  position: relative;
  background-color: var(--dominant);
  width: 100%;
  height: 100vh;
  flex-direction: column;
  padding: 0 var(--padding-36xl) var(--padding-3xl);
  justify-content: flex-start;
}
@media screen and (max-width: 1200px) {
  .navBar {
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
  .blankPayolaPage {
    z-index: 1000;
  }
}
@media screen and (max-width: 960px) {
  .frameWrapper {
    display: none;
  }
  .frame {
    display: flex;
  }
  .frameParent {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-left: 75%;
    padding-right: 0;
    box-sizing: border-box;
  }
  .navBar {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .frameParent {
    padding-left: 65%;
  }
  .blankPayolaPage {
    padding-left: var(--padding-6xl);
    padding-right: var(--padding-6xl);
    box-sizing: border-box;
  }
}
