@import url("https://fonts.googleapis.com/css2?family=Inter:ital,wght@0,400;0,600;0,700;1,400;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Alegreya:wght@700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --text-small: Inter;
  --font-roboto: Roboto;
  --font-source-code-pro: "Source Code Pro";
  --font-alegreya: Alegreya;
  --font-inherit: inherit;

  /* font sizes */
  --text-large-size: 1rem;
  --text-medium-size: 0.88rem;
  --text-small-size: 0.75rem;
  --font-size-21xl: 2.5rem;
  --font-size-mid: 1.06rem;
  --font-size-7xl: 1.63rem;
  --font-size-xl: 1.25rem;
  --font-size-mini: 0.94rem;
  --font-size-5xs: 0.5rem;
  --font-size-3xs: 0.63rem;
  --font-size-16xl: 2.19rem;
  --font-size-7xs: 0.38rem;
  --font-size-17xl: 2.25rem;
  --font-size-40xl: 3.69rem;

  /* Colors */
  --dominant: #fff;
  --studio-darkmode-exportbg-1a202e: #1a202e;
  --color-gray-100: #141414;
  --color-gray-200: #0b0b0b;
  --color-gray-300: #0a0a0a;
  --color-gray-400: rgba(0, 0, 0, 0.5);
  --color-gray-500: rgba(10, 10, 10, 0.5);
  --color-gray-600: rgba(0, 0, 0, 0.33);
  --color-gray-700: rgba(255, 255, 255, 0.55);
  --color-gray-800: rgba(255, 255, 255, 0.8);
  --color-gray-900: rgba(17, 17, 17, 0.75);
  --color-orange: #ef9e00;
  --light-background: #f3f6fd;
  --accent: #457eff;
  --color-gainsboro-100: #e7e7e7;
  --light-border: #e2e4ea;
  --studio-lightmode-midseparator-dee3eb: #dee3eb;
  --gray: #676f82;
  --studio-darkmode-popuplabels-5e6a86: #5e6a86;
  --dark-gray: #334158;
  --studio-darkmode-normalsolid-icons-96a2be: #96a2be;
  --color-dimgray: #495367;
  --studio-lightmode-lightseparator-ecf0f6: #ecf0f6;
  --color-whitesmoke: #ebeef2;
  --color-black: #000;
  --color-dodgerblue-100: #0085ff;
  --color-dodgerblue-200: #006bce;
  --color-darkorchid: #8528ce;

  /* Spacing */
  --default-border-raius: 0.25rem;

  /* Gaps */
  --gap-5xl: 1.5rem;
  --gap-11xs: 0.13rem;
  --gap-9xs: 0.25rem;
  --gap-3xs: 0.63rem;
  --gap-3xl: 1.38rem;
  --gap-xl: 1.25rem;
  --gap-base: 1rem;
  --gap-7xs: 0.38rem;
  --gap-5xs: 0.5rem;
  --gap-mini: 0.94rem;
  --gap-29xl: 3rem;
  --gap-smi: 0.81rem;
  --gap-6xs: 0.44rem;
  --gap-4xl: 1.44rem;
  --gap-sm: 0.88rem;

  /* Paddings */
  --padding-36xl: 3.44rem;
  --padding-3xl: 1.38rem;
  --padding-6xl: 1.56rem;
  --padding-69xl: 5.5rem;
  --padding-21xl: 2.5rem;
  --padding-5xl: 1.5rem;
  --padding-sm: 0.88rem;
  --padding-9xl: 1.75rem;
  --padding-13xl: 2rem;
  --padding-11xl: 1.88rem;
  --padding-61xl: 5rem;
  --padding-10xl: 1.81rem;
  --padding-xl: 1.25rem;
  --padding-3xs: 0.63rem;
  --padding-31xl: 3.13rem;
  --padding-base: 1rem;
  --padding-10xs: 0.19rem;
  --padding-7xs: 0.38rem;
  --padding-33xl: 3.25rem;
  --padding-8xl: 1.69rem;
  --padding-8xs: 0.31rem;

  /* Border radiuses */
  --br-5xs: 8px;
  --br-7xs: 6px;
  --br-3xs: 10px;
  --br-9xs: 4px;
  --br-11xl: 30px;
  --br-13xl: 32px;
  --br-6xl: 25px;
  --br-14xl: 33px;
}
